import axiosInstance from "./axiosInstance";

const getSearchSuggestions = async (query) => {
  const response = await axiosInstance.get("/getSearchData", {
    params: { query },
  });
  return response.data;
};

const getSearchResult = async (params) => {
  const response = await axiosInstance.get("/getSearchDataSecond", { params });
  return response.data;
};

export { getSearchSuggestions, getSearchResult };
