import { useAccordion } from "../contexts/accordionContext";
import { AnimatePresence, motion } from "framer-motion";
import { IoChevronDown } from "react-icons/io5";

export const AccordionItem = ({
  children,
  className = "w-full rounded-sm overflow-hidden bg-white border border-secondary mb-4",
}) => {
  return <div className={className}>{children}</div>;
};

export const AccordionHeader = ({
  children,
  className = "ACCORDION-HEADER w-full relative p-4 cursor-pointer select-none text-sm font-medium bg-white",
}) => {
  const { isActive, index, onChangeIndex } = useAccordion();

  return (
    <div onClick={() => onChangeIndex(index)} className={`${className}`}>
      {children}

      <div className="ACCORDION-ARROW absolute right-4 top-1/2 -translate-y-1/2">
        <IoChevronDown
          className={`${
            isActive && "rotate-180"
          } text-secondary transition-transform duration-200 ease-in-out`}
        />
      </div>
    </div>
  );
};

export const AccordionPanel = ({ children, className = "w-full p-4" }) => {
  const { isActive } = useAccordion();

  return (
    <AnimatePresence initial={false}>
      {isActive && (
        <motion.div
          initial={{ height: 0 }}
          animate={{ height: "auto" }}
          exit={{ height: 0 }}
          transition={{ type: "spring", duration: 0.4, bounce: 0 }}
        >
          <div className={className}>{children}</div>
        </motion.div>
      )}
    </AnimatePresence>
  );
};
