import HeroBanner from "../../components/HeroBanner";
import useMediaQuery from "../../hooks/useMediaQuery";
import Filter from "./Filter";
import MobileFilter from "./MobileFilter";
import ProductList from "./ProductList";

const Search = () => {
  const minLg = useMediaQuery("(min-width: 1024px)");

  return (
    <main>
      <div className="container max-w-full my-8 space-y-8">
        <HeroBanner position="Top" resourceType="Search" />

        <div className="PRODUCT-VIEW w-full flex max-lg:flex-col justify-between lg:gap-16 gap-4">
          {minLg ? <Filter /> : <MobileFilter />}
          <ProductList />
        </div>
      </div>
    </main>
  );
};

export default Search;
