import { createContext, useContext, useState, useMemo, Children } from "react";

const AccordionContext = createContext({});

export function AccordionProvider({
  children,
  multiple = false,
  defaultIndex = null,
  defaultOpen = false,
}) {
  const initialActiveIndices = useMemo(() => {
    if (defaultOpen) {
      return new Set([...Array(Children.count(children)).keys()]);
    } else if (multiple) {
      return new Set(defaultIndex !== null ? [defaultIndex] : []);
    } else {
      return defaultIndex !== null ? new Set([defaultIndex]) : new Set();
    }
  }, [children, defaultOpen, defaultIndex, multiple]);

  const [activeIndices, setActiveIndices] = useState(initialActiveIndices);

  const onChangeIndex = (index) => {
    setActiveIndices((prevActiveIndices) => {
      const newActiveIndices = new Set(prevActiveIndices);
      if (!multiple) {
        if (newActiveIndices.has(index)) {
          newActiveIndices.delete(index);
        } else {
          newActiveIndices.clear();
          newActiveIndices.add(index);
        }
      } else {
        if (newActiveIndices.has(index)) {
          newActiveIndices.delete(index);
        } else {
          newActiveIndices.add(index);
        }
      }
      return newActiveIndices;
    });
  };

  return children.map((child, index) => (
    <AccordionContext.Provider
      key={index}
      value={{
        isActive: activeIndices.has(index),
        index,
        onChangeIndex,
      }}
    >
      {child}
    </AccordionContext.Provider>
  ));
}

export function useAccordion() {
  return useContext(AccordionContext);
}
