import { useSearchParamsState } from "../../hooks/useSearchParamsState";
import Pagination from "../../components/Pagination";
import ProductBox from "../../components/ProductBox";
import { useSearchResult } from "../../services/searchServices";
import { useSearchParams } from "react-router-dom";
import useDebounce from "../../hooks/useDebounce";
import Loader from "../../components/Loader";

const ProductList = () => {
  const [searchParams] = useSearchParams();
  const [page, setPage] = useSearchParamsState("page", 1);
  const limit = 40;
  const query = searchParams.get("query");
  const sortBy = searchParams.get("sortBy");
  const price = useDebounce(searchParams.get("price"));
  console.log(price);
  const discount = searchParams.get("discount");
  const rating = searchParams.get("rating");
  const { data, isLoading, error } = useSearchResult({
    page,
    limit,
    query,
    sortBy,
    price,
    discount,
    rating,
  });

  if (isLoading || (!error && !data)) {
    return <Loader />;
  }

  if (error) {
    return (
      <div className="w-full">
        <h2 className="text-center font-secondary font-medium text-xl mt-8">
          {error.message}
        </h2>
      </div>
    );
  }

  return (
    <section className="w-full">
      {data?.variants?.length === 0 ? (
        <p className="text-center font-secondary font-medium text-xl">
          No Products Found
        </p>
      ) : (
        <div className="flex flex-wrap justify-center gap-8 max-2xl:gap-6 max-xl:gap-4 max-sm:gap-2">
          {data?.variants?.map((productItem, index) => (
            <ProductBox key={index} product={productItem} />
          ))}
        </div>
      )}

      <div className="PAGINATION">
        <Pagination
          currentPage={parseInt(page, 10)}
          totalCount={data?.totalItems}
          pageSize={limit}
          onPageChange={(page) => setPage(page.toString())}
        />
      </div>
    </section>
  );
};

export default ProductList;
