import { keepPreviousData, useQuery } from "@tanstack/react-query";
import { getSearchResult, getSearchSuggestions } from "../api/searchApis";

const useSearchSuggestions = (query) => {
  return useQuery({
    queryKey: ["suggestions", query],
    queryFn: () => getSearchSuggestions(query),
    enabled: !!query,
    staleTime: 5 * 60 * 1000,
    placeholderData: keepPreviousData,
    retry: false,
  });
};

const useSearchResult = (params) => {
  return useQuery({
    queryKey: ["search", params],
    queryFn: () => getSearchResult(params),
    staleTime: 5 * 60 * 1000,
    placeholderData: keepPreviousData,
  });
};

export { useSearchSuggestions, useSearchResult };
